import React, { Component } from "react";
import Navbar from "../../components/Navbar/DashNav";

export default class Jobs extends Component {
	render() {
		return (
			<div>
				<Navbar />
				<section
					className="search-area"
					style={{
						backgroundImage:
							"url(https://hrcdn.net/fcore/assets/svgs/world_map-e1ca0120b6.svg)",
					}}
				>
					<div className="search-shadow"></div>
				</section>
				<section className="py-5 jobs-area">
					<div className="container">
						<div className="row">
							<div className="col-md-4 my-3">
								<div className="row no-gutters job">
									<div className="col-md-4">
										<div
											className="logo"
											style={{
												backgroundImage:
													"url(https://futurestartup.com/wp-content/uploads/2019/10/augmedix.jpg)",
											}}
										></div>
									</div>
									<div className="col-md-8">
										<div className="job-content">
											<h3 className="title">Android Developer</h3>
											<div className="d-flex mt-1">
												<i className="building outline icon"></i>
												<p className="pl-1">Augmedix Limited</p>
											</div>
											<div className="d-flex mt-1">
												<i className="map marker alternate icon"></i>
												<p className="pl-1">Dhaka, Bangladesh</p>
											</div>
											<div className="d-flex mt-1">
												<i className="briefcase icon"></i>
												<p className="pl-1">Fresher</p>
											</div>
										</div>
									</div>
								</div>
							</div>
							<div className="col-md-4 my-3">
								<div className="row no-gutters job">
									<div className="col-md-4">
										<div
											className="logo"
											style={{
												backgroundImage: `url(data:image/png;base64,)`,
											}}
										></div>
									</div>
									<div className="col-md-8">
										<div className="job-content">
											<h3 className="title">Web Developer</h3>
											<div className="d-flex mt-1">
												<i className="building outline icon"></i>
												<p className="pl-1">Brainstation 23</p>
											</div>
											<div className="d-flex mt-1">
												<i className="map marker alternate icon"></i>
												<p className="pl-1">Dhaka, Bangladesh</p>
											</div>
											<div className="d-flex mt-1">
												<i className="briefcase icon"></i>
												<p className="pl-1">Fresher</p>
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</section>
			</div>
		);
	}
}
